/* HeroSection.css */

.mainhero {
  background-image: url(/static/media/office2.90a85c0e.jpg);
  background-size: cover; /* Ensure the background covers the entire area */
  width: 100%;
  height: 775px;
  overflow: hidden;
}
.Hero {
  font-family: "Barlow", sans-serif;
  background: linear-gradient(90deg, #f18484 0%, rgba(238, 0, 114, 0.41) 100%);
  width: 100%;
  height: 775px;
  position: absolute;
  display: flex;
  justify-content: center;
  padding: 1.2rem;
  color: white;
  padding-left: 9%;
  overflow: hidden;
  margin: auto;
  padding-left: 9%;
  padding-right: 20vh;

  h1 {
    font-size: 62px;
    font-weight: 700;
    line-height: 74px;
    display: flex;
    justify-content: left;
  }

  .innerheding {
    position: absolute;
    font-size: 128px;
    line-height: 128px;
    color: rgb(0, 241, 161);
    font-weight: 700;
  }
}

.Heroheadings {
  padding-left: 9%;
  padding-right: 20vh;
  width: 70%;
  margin-top: 70px;
  display: flex;
  flex-direction: column;
  justify-content: left;
  position: relative;
  animation: slide-in 1s ease-out forwards;
  opacity: 0;
}
.roteting {
  margin-bottom: 40px;
}
.roteting .innerheding {
  transition: opacity 1s ease-in-out;
  -webkit-transition: opacity 1s ease-in-out;
  -moz-transition: opacity 1s ease-in-out;
  -ms-transition: opacity 1s ease-in-out;
  -o-transition: opacity 1s ease-in-out;
}

.herodes {
  margin-top: 18%;
  width: 75%;

  h2 {
    font-size: 38px;
    text-align: left;
    line-height: 40px;
  }
}

.mainhero {
  overflow: hidden;
}

@keyframes slide-in {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes arrow-slide-in {
  0% {
    transform: translateX(100%); /* Start off-screen */
  }
  100% {
    transform: translateX(0); /* Final position */
  }
}

@keyframes arrow-color-change {
  0% {
    background-color: red;
  }
  100% {
    background-color: #282c35;
  }
}

@keyframes arrow-color-change-alt {
  0% {
    background-color: #282c35;
  }
  100% {
    background-color: red;
  }
}

.arrow {
  position: absolute;
  width: 200px;
  /*Adjustsizeasneeded*/
  height: 400px;
  /*Adjustsizeasneeded*/
  -webkit-clip-path: polygon(54% 0, 100% 50%, 54% 100%, 0 100%, 51% 50%, 0 0);
          clip-path: polygon(54% 0, 100% 50%, 54% 100%, 0 100%, 51% 50%, 0 0);
  cursor: pointer;
  animation: arrow-slide-in 2s cubic-bezier(0.42, 0, 0.58, 1) forwards,
    arrow-color-change 1s cubic-bezier(0.42, 0, 0.58, 1) forwards 1s;
  -webkit-animation: arrow-slide-in 2s cubic-bezier(0.42, 0, 0.58, 1) forwards,
    arrow-color-change 1s cubic-bezier(0.42, 0, 0.58, 1) forwards 1s;
}

.arrow-up {
  top: 10px;
  right: -200px; /* Start off-screen */
  background: red; /* Initial color for arrow 1 */
  animation-delay: 0.5s, 1.5s; /* Delay to match Heroheadings and then color change */
}

.arrow-down {
  top: 350px;
  right: -380px; /* Start off-screen */
  background: #282c35; /* Initial color for arrow 2 */
  animation: arrow-slide-in 1s cubic-bezier(0.42, 0, 0.58, 1) forwards,
    arrow-color-change-alt 1s cubic-bezier(0.42, 0, 0.58, 1) forwards 1s;
  animation-delay: 0.5s, 1.5s; /* Delay to match Heroheadings and then color change */
}

