/* Hero Section */
.ourthinking_hero {
    background-image: url('../assets/our.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 72px 12% 72px 12%;
}

.ourthinking_content h1 {
    font-size: 62px;
    font-weight: 700;
    line-height: 74px;
    color: #ffffff;
}

.ourthinking_content p {
    font-size: 20px;
    line-height: 26px;
    font-weight: 500;
    color: #ffffff;
   
}
.card_section{
    background-color: #262626;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 90px 0px;
}
.infocards{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 0px;
    flex-wrap: wrap;
    gap: 60px;
}

.card-title {
    color: #262626;
    font-size: 1.5em;
    line-height: normal;
    font-weight: 700;
    margin-bottom: 0.5em;
  }
  
  .small-desc {
    font-size: 1em;
    font-weight: 400;
    line-height: 1.5em;
    color: #452c2c;
  }
  
  .small-desc {
    font-size: 1em;
  }
  
  .go-corner {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 2em;
    height: 2em;
    overflow: hidden;
    top: 0;
    right: 0;
    background: linear-gradient(135deg, #6293c8, #384c6c);
    border-radius: 0 4px 0 32px;
  }
  
  .go-arrow {
    margin-top: -4px;
    margin-right: -4px;
    color: white;
    font-family: courier, sans;
  }
  
  .infocard {
    display: block;
    position: relative;
    max-width: 300px;
    height: 300px;
    background-color: #f2f8f9;
    border-radius: 10px;
    padding: 2em 1.2em;
    margin: 12px;
    text-decoration: none;
    z-index: 0;
    overflow: hidden;
    background: linear-gradient(to bottom, #c3e6ec, #a7d1d9);
    font-family: Arial, Helvetica, sans-serif;
  }
  
  .infocard:before {
    content: '';
    position: absolute;
    z-index: -1;
    top: -16px;
    right: -16px;
    background: linear-gradient(135deg, #364a60, #384c6c);
    height: 32px;
    width: 32px;
    border-radius: 32px;
    transform: scale(1);
    transform-origin: 50% 50%;
    transition: transform 0.35s ease-out;
  }
  
  .infocard:hover:before {
    transform: scale(28);
  }
  
  .infocard:hover .small-desc {
    transition: all 0.5s ease-out;
    color: rgba(255, 255, 255, 0.8);
  }
  
  .infocard:hover .card-title {
    transition: all 0.5s ease-out;
    color: #ffffff;
  }
  